<script>
import { mapActions, mapGetters} from "vuex";
import "select2";
import Step4 from "@/components/perwalian/Step4"
import axios from "axios";
export default {
  name: "Step3",
  inject:['notyf'],
  data() {
    return {
      matkulwajib: [],
      matkulup: [],
      matkuldown: [],
      tempData: [],
      list_curriculum: [],
      list_credit: [],
      list_guardianship: [],
    };
  },
  computed: {
    ...mapGetters({
      getDataStep3: "step3/step3",
      getDataMatkul: "jumlahmatkul/matkul",
      getDataMatkulWajib: "pilihmatkulperwalian/matkulwajib",
      getDataMatkulUp: "pilihmatkulperwalian/matkulup",
      getDataMatkulDown: "pilihmatkulperwalian/matkuldown",
      getApproveStatus: "onepage/perwalian",
      authenticated: "auth/authenticated",
    }),
  },
  methods: {
    async sendMatkulCheck() {
      let datamatkul = {
        guardianship_id : this.list_guardianship,
        curriculum_id : this.list_curriculum,
        credits : this.list_credit
      }
      
      try {
        let response = await axios.post(
          "/oasis/guardianship-detail-add",
          datamatkul
        );
        if (response.data.meta.status === "success") {
          this.notyf.success({
            message: response.data.meta.message,
            dismissible: false,
            duration: 5000,
          });
          this.getMatkulALL();
        } else {
          this.notyf.error({
            message: response.data.meta.message,
            dismissible: true,
            
          });
        }
        return response;
      } catch (error) {
        console.warn(error)
      }
    },
    setMatkul() {
      this.list_credit = [];
      this.list_curriculum = [];
      this.list_guardianship = [];
      if (this.tempData.length > 0) {
        for (let i = 0; i < this.tempData.length; i++) {
          let datasplit = this.tempData[i];
          let data = datasplit.split("/");
          let curriculum = data[0];
          let credit = data[1];
          let guardianship_id = data[2];
          if (!this.list_curriculum.includes(curriculum)) {
            this.list_curriculum.push(curriculum);
            this.list_guardianship.push(guardianship_id);
            this.list_credit.push(credit);
          }
        }
      }
    },
    ...mapActions({
      perwalian: "step3/step3",
      matkulall: "jumlahmatkul/matkul",
      pilihmatkul: "pilihmatkulperwalian/pilihMatkul",
    }),
    number: function (index) {
      return index + 1;
    },
    getPerwalian() {
      this.perwalian();
    },
    getMatkulALL() {
      this.matkulall();
    },
    getMatkulData() {
      this.pilihmatkul();
    },
  },

  mounted() {
    this.getPerwalian();
    this.getMatkulALL();
    this.getMatkulData();
    $(".select-2").select2();
    console.log(this.getDataStep3.full_name)
  },
  components: {
    Step4
  },
};
</script>

